/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
}

/* Authentication Styles */
.auth-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
}

.auth-box {
  background: white;
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 20px 40px rgba(0,0,0,0.1);
  width: 100%;
  max-width: 400px;
  text-align: center;
}

.auth-box h1 {
  color: #333;
  margin-bottom: 30px;
  font-size: 24px;
}

.auth-tabs {
  display: flex;
  margin-bottom: 20px;
  border-radius: 8px;
  overflow: hidden;
  background: #f5f5f5;
  border: 1px solid #e1e1e1;
}

.auth-tabs button {
  flex: 1;
  padding: 12px;
  border: none;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s;
  font-weight: 500;
}

.auth-tabs button.active {
  background: #667eea;
  color: white;
}

.auth-tabs button:not(.active):hover {
  background: #e8ecfa;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 20px;
}

.auth-form input {
  padding: 12px;
  border: 2px solid #e1e1e1;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s;
}

.auth-form input:focus {
  outline: none;
  border-color: #667eea;
}

.auth-form button {
  padding: 12px;
  background: #667eea;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.3s;
}

.auth-form button:hover {
  background: #5a6fd8;
}

.auth-form button:disabled {
  background: #ccc;
  cursor: not-allowed;
}

.auth-actions {
  display: flex;
  gap: 10px;
}

.auth-actions button {
  flex: 1;
}

.employee-login-info {
  text-align: center;
  margin-bottom: 15px;
  padding: 10px;
  background: #e8ecfa;
  border-radius: 6px;
  color: #667eea;
  font-weight: 500;
}

.employee-login-btn {
  background: #10ac84 !important;
  margin-bottom: 15px;
}

.employee-login-btn:hover {
  background: #0d956c !important;
}

.employee-credentials-hint {
  background: #e8ecfa;
  padding: 15px;
  border-radius: 8px;
  margin-top: 15px;
  border-left: 4px solid #667eea;
}

.employee-credentials-hint p {
  margin: 5px 0;
  font-size: 14px;
}

.employee-credentials-hint code {
  background: #333;
  color: #fff;
  padding: 2px 6px;
  border-radius: 4px;
  font-family: 'Courier New', monospace;
}

/* Enhanced Employee Panel Styles */
.employee-admin-panel {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background: white;
  min-height: 100vh;
  position: relative;
}

.employee-info {
  background: #e8ecfa;
  padding: 10px 15px;
  border-radius: 8px;
  margin: 15px 0;
  border-left: 4px solid #667eea;
}

.employee-info p {
  margin: 0;
  color: #333;
  font-weight: 500;
}

.admin-header {
  text-align: center;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 2px solid #f0f0f0;
}

.admin-header h1 {
  color: #333;
  margin-bottom: 10px;
}

.admin-header p {
  color: #666;
  font-size: 16px;
}

.admin-section {
  background: #f8f9fa;
  padding: 25px;
  border-radius: 10px;
  margin-bottom: 25px;
  border: 1px solid #e9ecef;
}

.admin-section h2 {
  color: #333;
  margin-bottom: 15px;
  font-size: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.section-description {
  color: #666;
  margin-bottom: 20px;
  line-height: 1.5;
  background: white;
  padding: 15px;
  border-radius: 6px;
  border-left: 4px solid #667eea;
}

.global-url-config {
  background: white;
  padding: 20px;
  border-radius: 8px;
  border: 1px solid #e1e1e1;
}

.input-hint {
  display: block;
  margin-top: 5px;
  color: #666;
  font-size: 12px;
}

.current-config {
  margin-top: 25px;
  padding: 20px;
  background: #f8f9fa;
  border-radius: 8px;
}

.current-config h4 {
  margin-bottom: 15px;
  color: #333;
}

.config-display {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #e1e1e1;
}

.config-display:last-child {
  border-bottom: none;
}

.config-value {
  font-family: 'Courier New', monospace;
  background: #e9ecef;
  padding: 5px 10px;
  border-radius: 4px;
  color: #333;
}

.config-value.empty {
  color: #ff4757;
  font-style: italic;
}

.system-info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
}

.info-item {
  background: white;
  padding: 15px;
  border-radius: 6px;
  border: 1px solid #e1e1e1;
}

.info-item strong {
  color: #333;
  display: block;
  margin-bottom: 5px;
}

.admin-actions {
  text-align: center;
  margin-top: 30px;
  padding-top: 20px;
  border-top: 2px solid #f0f0f0;
}

/* Main App Styles */
.app-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.app-header {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 15px 20px;
  box-shadow: 0 2px 20px rgba(0,0,0,0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.header-left h1 {
  color: #333;
  font-size: 24px;
  margin-bottom: 5px;
}

.user-info {
  background: #667eea;
  color: white;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: bold;
}

.nav-tabs {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
}

.nav-btn {
  padding: 10px 15px;
  border: none;
  background: transparent;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s;
  font-size: 14px;
  color: #666;
}

.nav-btn:hover {
  background: #f0f0f0;
}

.nav-btn.active {
  background: #667eea;
  color: white;
}

.logout-btn {
  padding: 8px 16px;
  background: #ff4757;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.3s;
}

.logout-btn:hover {
  background: #ff3742;
}

.main-content {
  flex: 1;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}

.tab-content {
  background: white;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  margin-bottom: 20px;
}

.tab-content h2 {
  color: #333;
  margin-bottom: 25px;
  font-size: 24px;
  border-bottom: 2px solid #f0f0f0;
  padding-bottom: 10px;
}

/* Form Styles */
.form-group {
  margin-bottom: 25px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: #333;
}

.form-group input,
.form-group textarea,
.form-group select {
  width: 100%;
  padding: 12px;
  border: 2px solid #e1e1e1;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s;
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: #667eea;
}

.form-group textarea {
  min-height: 80px;
  resize: vertical;
}

.control-input {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

.gpio-badge {
  background: #667eea;
  color: white;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: bold;
}

/* Button Styles */
button {
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.3s;
}

button.primary {
  background: #667eea;
  color: white;
}

button.primary:hover {
  background: #5a6fd8;
}

button.secondary {
  background: #f8f9fa;
  color: #333;
  border: 1px solid #dee2e6;
}

button.secondary:hover {
  background: #e9ecef;
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.save-btn {
  background: #10ac84;
  color: white;
  margin-bottom: 20px;
}

.save-btn:hover {
  background: #0d956c;
}

/* Project Save Section */
.project-save-section {
  margin-bottom: 30px;
  padding: 20px;
  background: #f8f9fa;
  border-radius: 8px;
}

.saved-projects h3 {
  margin: 20px 0 10px 0;
  color: #333;
}

.projects-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 10px;
}

.project-item {
  background: white;
  padding: 15px;
  border-radius: 8px;
  border: 1px solid #e1e1e1;
  cursor: pointer;
  transition: all 0.3s;
}

.project-item:hover {
  border-color: #667eea;
  transform: translateY(-2px);
}

.project-item span {
  font-weight: bold;
  display: block;
  margin-bottom: 5px;
}

.project-item small {
  color: #666;
}

/* Code Editor Styles */
.code-mode-selector {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.code-mode-selector label {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.editor-actions {
  margin-bottom: 10px;
}

.code-editor-container {
  border: 2px solid #e1e1e1;
  border-radius: 8px;
  overflow: hidden;
}

/* Library Search */
.library-search {
  display: flex;
  gap: 10px;
  margin-bottom: 15px;
}

.library-search input {
  flex: 1;
}

.search-results {
  margin: 15px 0;
  max-height: 300px;
  overflow-y: auto;
}

.search-results h4 {
  margin-bottom: 10px;
  color: #333;
}

.library-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border: 1px solid #e1e1e1;
  border-radius: 6px;
  margin-bottom: 8px;
}

.library-info {
  display: flex;
  flex-direction: column;
}

.library-info strong {
  color: #333;
}

.library-info span {
  color: #666;
  font-size: 12px;
}

.install-btn {
  background: #10ac84;
  color: white;
  padding: 6px 12px;
  font-size: 12px;
}

.install-btn:hover {
  background: #0d956c;
}

.libraries-input {
  margin-top: 15px;
}

/* Controls Grid */
.controls-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
}

.control-card {
  background: #f8f9fa;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  border: 1px solid #e1e1e1;
}

.control-card h3 {
  margin-bottom: 10px;
  color: #333;
}

.gpio-info {
  color: #667eea;
  font-weight: bold;
  margin-bottom: 15px;
}

.control-actions {
  display: flex;
  gap: 10px;
  justify-content: center;
}

.control-actions button {
  flex: 1;
  padding: 8px 12px;
}

.control-actions button:first-child {
  background: #10ac84;
  color: white;
}

.control-actions button:last-child {
  background: #ff4757;
  color: white;
}

.no-controls {
  text-align: center;
  padding: 40px;
  color: #666;
}

/* Log Styles */
.log-section {
  margin-bottom: 30px;
}

.log-controls {
  margin-bottom: 15px;
}

.log-container {
  background: #1e1e1e;
  color: #fff;
  padding: 15px;
  border-radius: 8px;
  max-height: 400px;
  overflow-y: auto;
  font-family: 'Courier New', monospace;
  font-size: 14px;
}

.log-entry {
  margin-bottom: 4px;
  word-break: break-all;
}

.log-empty {
  color: #666;
  font-style: italic;
}

.serial-monitor {
  background: #2d2d2d;
}

/* Job Status */
.job-status {
  margin-top: 20px;
}

.job-item {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 10px;
  border: 1px solid #e1e1e1;
  border-radius: 6px;
  margin-bottom: 8px;
  background: #f8f9fa;
}

.job-item.success {
  border-color: #10ac84;
  background: #d1f2eb;
}

.job-item.error {
  border-color: #ff4757;
  background: #ffeaea;
}

.job-item.processing {
  border-color: #667eea;
  background: #e8ecfa;
}

.job-id {
  font-family: 'Courier New', monospace;
  font-size: 12px;
  color: #666;
}

.job-status {
  font-weight: bold;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 12px;
}

.job-item .job-status {
  background: #667eea;
  color: white;
}

.job-item.success .job-status {
  background: #10ac84;
}

.job-item.error .job-status {
  background: #ff4757;
}

/* Report Styles */
.report-container {
  margin-top: 20px;
}

.report-textarea {
  width: 100%;
  min-height: 300px;
  padding: 15px;
  border: 1px solid #e1e1e1;
  border-radius: 8px;
  font-family: 'Courier New', monospace;
  font-size: 14px;
  resize: vertical;
}

.report-actions {
  margin-top: 15px;
  display: flex;
  gap: 10px;
}

/* Camera Styles */
.camera-view {
  text-align: center;
}

.camera-feed {
  max-width: 100%;
  max-height: 500px;
  border-radius: 8px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.2);
}

.camera-error {
  color: #ff4757;
  padding: 40px;
}

.camera-placeholder {
  color: #666;
  padding: 60px 20px;
  background: #f8f9fa;
  border-radius: 8px;
}

.stream-info {
  margin-top: 20px;
  color: #666;
  font-size: 14px;
}

.camera-status {
  margin-top: 10px;
  padding: 10px;
  border-radius: 6px;
  background: #f8f9fa;
}

/* Enhanced Port Configuration Styles */
.port-configuration {
  background: #f8f9fa;
  padding: 20px;
  border-radius: 10px;
  border: 1px solid #e1e1e1;
}

.port-inputs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-bottom: 20px;
}

.port-select {
  width: 100%;
  padding: 12px;
  border: 2px solid #e1e1e1;
  border-radius: 8px;
  font-size: 16px;
  background: white;
}

.port-select:focus {
  outline: none;
  border-color: #667eea;
}

/* Ports Display Section */
.ports-display {
  background: white;
  padding: 20px;
  border-radius: 10px;
  border: 1px solid #e1e1e1;
}

.ports-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 2px solid #f0f0f0;
}

.quick-actions {
  display: flex;
  align-items: center;
  gap: 10px;
}

.quick-actions small {
  color: #666;
  font-weight: 500;
}

.small-btn {
  padding: 6px 12px;
  font-size: 12px;
  background: #e9ecef;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s;
}

.small-btn:hover:not(:disabled) {
  background: #667eea;
  color: white;
}

.small-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Ports List Container */
.ports-list-container {
  max-height: 400px;
  overflow-y: auto;
  border: 1px solid #e1e1e1;
  border-radius: 8px;
}

.ports-list {
  display: flex;
  flex-direction: column;
}

.port-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  border-bottom: 1px solid #f0f0f0;
  transition: background 0.3s;
}

.port-item:hover {
  background: #f8f9fa;
}

.port-item:last-child {
  border-bottom: none;
}

.port-info {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.port-name {
  font-weight: bold;
  color: #333;
  font-family: 'Courier New', monospace;
}

.port-status {
  font-size: 12px;
  color: #10ac84;
  background: #d1f2eb;
  padding: 2px 8px;
  border-radius: 12px;
  display: inline-block;
  width: fit-content;
}

.port-actions {
  display: flex;
  gap: 10px;
}

.action-btn {
  padding: 8px 12px;
  font-size: 12px;
  border: 1px solid #dee2e6;
  border-radius: 6px;
  background: white;
  cursor: pointer;
  transition: all 0.3s;
}

.action-btn:hover {
  background: #f8f9fa;
  border-color: #667eea;
}

.action-btn.active {
  background: #10ac84;
  color: white;
  border-color: #10ac84;
}

/* No Ports State */
.no-ports {
  text-align: center;
  padding: 40px 20px;
  color: #666;
}

.no-ports p {
  margin-bottom: 15px;
  font-size: 16px;
}

/* Current Configuration Status */
.current-config-status {
  background: white;
  padding: 20px;
  border-radius: 8px;
  border: 1px solid #e1e1e1;
}

.config-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #f0f0f0;
}

.config-item:last-child {
  border-bottom: none;
}

.config-item strong {
  color: #333;
}

.config-value {
  font-family: 'Courier New', monospace;
  background: #e9ecef;
  padding: 6px 12px;
  border-radius: 6px;
  color: #333;
}

.config-value.empty {
  color: #ff4757;
  font-style: italic;
}

/* Scrollbar Styling for Ports List */
.ports-list-container::-webkit-scrollbar {
  width: 8px;
}

.ports-list-container::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.ports-list-container::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
}

.ports-list-container::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* Loading Overlay */
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.7);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  color: white;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 5px solid rgba(255,255,255,0.3);
  border-top: 5px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 15px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Board Selection */
.board-selection {
  display: flex;
  gap: 10px;
  align-items: center;
  flex-wrap: wrap;
}

.board-selection select {
  flex: 1;
  min-width: 200px;
}

.preference-info {
  margin-top: 8px;
  color: #10ac84;
  font-style: italic;
}

/* Upload Area */
.upload-area {
  border: 2px dashed #e1e1e1;
  padding: 30px;
  border-radius: 8px;
  text-align: center;
  transition: border-color 0.3s;
}

.upload-area:hover {
  border-color: #667eea;
}

.file-preview {
  margin-top: 15px;
  padding: 10px;
  background: #f8f9fa;
  border-radius: 6px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .app-header {
    flex-direction: column;
    gap: 15px;
  }
  
  .nav-tabs {
    order: 3;
    width: 100%;
    justify-content: center;
  }
  
  .main-content {
    padding: 10px;
  }
  
  .tab-content {
    padding: 20px;
  }
  
  .controls-grid {
    grid-template-columns: 1fr;
  }
  
  .board-selection {
    flex-direction: column;
    align-items: stretch;
  }
  
  .action-buttons {
    flex-direction: column;
  }
  
  .employee-admin-panel {
    padding: 15px;
  }
  
  .admin-section {
    padding: 15px;
  }
  
  .config-display {
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
  }
  
  .system-info {
    grid-template-columns: 1fr;
  }
  
  .auth-actions {
    flex-direction: column;
  }
  
  .port-inputs {
    grid-template-columns: 1fr;
  }
  
  .port-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }
  
  .port-actions {
    width: 100%;
    justify-content: space-between;
  }
  
  .ports-header {
    flex-direction: column;
